import React from 'react';
import { Link } from 'react-router-dom';
import './query.css';
import $ from 'jquery';
import HeaderTop from '../../components/HeaderTop';
import FormSub from '../../components/FormSub';
import FooterMid from '../../components/FooterMid';
import Helmet from 'react-helmet';

export default function OrderSuccess() {
  function hidefn() {
    let button = $('.order');
    if (!button.hasClass('animate')) {
      button.addClass('animate');
      setTimeout(() => {
        button.removeClass('animate');
        $('.orderall').addClass('hide');
      }, 10000);
    }
    let success1 = $('.success1');
    if (success1.hasClass('hide')) {
      setTimeout(() => {
        success1.removeClass('hide');
      }, 10000);
    }
  }
  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>OrderSuccess page</title>
        <meta name="description" content="Get all furniture in kerala" />
        <link rel="canonical" href="http://mysite.com/example" />
      </Helmet>
      {window.innerWidth >= 830 ? (
        <header className="App-header border-bottom">
          <HeaderTop />
        </header>
      ) : (
        ''
      )}

      <main>
        <div className="text-center">
          <div className="orderall">
            <div>
              <img
                style={{ height: '64px' }}
                src="/waiting.png"
                className="img-fluid"
                alt="success"
              />
              <h5>.....</h5>
              <h1>Place</h1>
              <h1 className="mb-4">Your order now...</h1>
            </div>
            <div className="success2">
              <button onClick={hidefn} class="order">
                <span class="default">Complete Order</span>
                <span class="success">
                  Order Placed
                  <svg viewbox="0 0 12 10">
                    <polyline points="1.5 6 4.5 9 10.5 1"></polyline>
                  </svg>
                </span>
                <div class="box"></div>
                <div class="truck">
                  <div class="back"></div>
                  <div class="front">
                    <div class="window"></div>
                  </div>
                  <div class="light top"></div>
                  <div class="light bottom"></div>
                </div>
                <div class="lines"></div>
              </button>
              <a
                class="dribbble"
                href="https://dribbble.com/shots/7112021-Order-confirm-animation"
                target="_blank"
                rel="noreferrer"
              >
                <img alt="" src="placeorder.mp4" />
              </a>
              <div className="mt-4">
                <h5>Billing Details</h5>
                <p>
                  Niyaskm
                  <br />
                  Kizhakkan house
                  <br />
                  Ponjassery p.o
                </p>
              </div>
            </div>
          </div>

          <div className="success1 hide">
            <div className="success1">
              <img
                style={{ height: '120px' }}
                src="/tik.png"
                className="img-fluid"
                alt="success"
              />
              <h5>Awesome</h5>
              <h1>Congratulations.</h1>
              <h1 className="mb-4">Your order is accepted</h1>
              <div className="success2">
                <Link className="success-btn" to="/">
                  Continue Shopping
                </Link>
                <p className="mt-4">
                  Have patience our customer executive will contact you soon
                </p>
              </div>
            </div>
          </div>
        </div>
      </main>
      <footer>
        <FormSub />
        <FooterMid />
      </footer>
    </>
  );
}
