import './edit.scss';
import DriveFolderUploadOutlinedIcon from '@mui/icons-material/DriveFolderUploadOutlined';
import { useContext, useEffect, useState } from 'react';
import { Button, Form } from 'react-bootstrap';
import Sidebar from '../../../components/sidebar/Sidebar';
import useFetch from '../../../../hooks/useFetch';
import { useNavigate, useParams } from 'react-router-dom';
import { productsInputs } from '../../../../formSource';
import axios from 'axios';
import { Store } from '../../../../Store';

const EditProduct = () => {
  const navigate = useNavigate();
  const { state, dispatch: ctxDispatch } = useContext(Store);
  useEffect(() => {
    (state.user === null || state.user.user === null) && navigate('/login');
  }, [state.user]);

  const [file, setFile] = useState('');
  const { id } = useParams();

  const { data, loading, error } = useFetch(
    `http://localhost:5000/api/products/${id}`
  );
  const [image, setImage] = useState(data.image);
  const [category, setCategory] = useState(data.category);
  const [subCategory, setSubCategory] = useState(data.subCategory);
  const editfile = {
    image: data.image,
    category: category,
    subcategory: subCategory,
  };
  console.log(editfile);
  const handleClick = async () => {
    await axios.put(`http://localhost:5000/api/products/${id}`, editfile);
  };

  return (
    <div className="new">
      <Sidebar />
      <div className="newContainer">
        <div className="top">
          <h1>Edit Product</h1>
        </div>
        <div className="main-set">
          <div className="text-center">
            <img
              className="img-fluid"
              src={file ? URL.createObjectURL(file) : data.image}
              alt=""
            />
          </div>

          <div>
            <Form>
              <Form.Group className="formInput">
                <Form.Label htmlFor="file">
                  Image upload:{' '}
                  <DriveFolderUploadOutlinedIcon className="icon" />
                </Form.Label>
                <Form.Control
                  type="file"
                  id="file"
                  onChange={(e) => setFile(e.target.files[0])}
                  style={{ display: 'none' }}
                />
              </Form.Group>

              <Form.Group className="mb-3">
                <Form.Label>Category:</Form.Label>
                <Form.Select>
                  <option>{data.category}</option>
                  {productsInputs[0].category?.map((e) => (
                    <option value={e}>{e}</option>
                  ))}
                </Form.Select>
              </Form.Group>
              <Form.Group className="mb-3">
                <Form.Label>Sub Category:</Form.Label>
                <Form.Select>
                  <option>{data.subcategory}</option>
                  {productsInputs[1].subCategory?.map((e) => (
                    <option value={e}>{e}</option>
                  ))}
                </Form.Select>
              </Form.Group>
              <Button onClick={() => handleClick} type="submit">
                Submit
              </Button>
            </Form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default EditProduct;
