import React from 'react';
import Rating from '../../../components/Rating';

export default function Reviews() {
  return (
    <>
      <h2 className="with-stroke text-center ">
        <span className="stroke-head">Customer Stories</span>
      </h2>
      <div className="review-cont">
        <div className="review-each border rounded">
          <img
            style={{ borderRadius: '240px', height: '120px' }}
            className="img-fluid p-3"
            src="/images/review/rev1.jpg"
            alt="rev1"
          />
          <h4>Suhail Ma</h4>
          <p style={{ color: 'grey' }}>Ernakulam,kerala</p>
          <p>"Having beautiful furniture</p>
          <Rating rating="4" />
        </div>

        <div className="review-each border rounded">
          <img
            style={{ borderRadius: '240px', height: '120px' }}
            className="img-fluid p-3"
            src="/images/review/rev2.jpg"
            alt="rev1"
          />
          <h4>Manaf km</h4>
          <p style={{ color: 'grey' }}>Ernakulam,kerala</p>
          <p>"Having beautiful furniture</p>
          <Rating rating="5" />
        </div>

        <div className="review-each border rounded">
          <img
            style={{ borderRadius: '240px', height: '120px' }}
            className="img-fluid p-3"
            src="/images/review/rev3.jpg"
            alt="rev1"
          />
          <h4>Niyas km</h4>
          <p style={{ color: 'grey' }}>Ernakulam,kerala</p>
          <p>"Having beautiful furniture</p>
          <Rating rating="4.5" />
        </div>
      </div>
    </>
  );
}
