import './orderScreen.scss';
import Sidebar from '../../components/sidebar/Sidebar';
import { Button, Col, Container, Row, Table } from 'react-bootstrap';
import { Link, useNavigate } from 'react-router-dom';
import { useContext, useEffect } from 'react';
import { Store } from '../../../Store';
import useFetch from '../../../hooks/useFetch';
import axios from 'axios';

const OrderScreen = () => {
  const navigate = useNavigate();
  const { state, dispatch: ctxDispatch } = useContext(Store);
  useEffect(() => {
    (state.user === null || state.user.user === null) && navigate('/login');
  }, [state.user]);
  const { data, loading, error } = useFetch('http://localhost:5000/api/order/');
  const handleView = (e) => {
    navigate(`/admin/orders/${e}`);
  };
  const handleDelete = async (e) => {
    navigate('/admin');
    await axios.delete(`http://localhost:5000/api/order/${e}`);
    navigate('/admin/orders');
  };
  return (
    <Container fluid className="home">
      <Row>
        <Col md={3}>
          <Sidebar />
        </Col>
        <Col md={9} className="homeContainer">
          <div className="datatable">
            <div className="datatableTitle">Orders</div>
            <Table style={{ width: '70vw' }} striped bordered hover>
              <thead>
                <tr>
                  <th>#</th>
                  <th>Name</th>
                  <th>Email</th>
                  <th>Phone Number</th>
                </tr>
              </thead>
              <tbody>
                {data.map((e, i) => (
                  <tr>
                    <td>{i + 1}</td>
                    <td>{e.name}</td>
                    <td>{e.email}</td>
                    <td>{e.number}</td>
                    <div className="d-flex">
                      <Button
                        onClick={() => handleView(e._id)}
                        variant="success"
                        className="viewButton"
                      >
                        View
                      </Button>

                      <Button
                        variant="danger"
                        className="deleteButton"
                        onClick={() => handleDelete(e._id)}
                      >
                        Delete
                      </Button>
                    </div>
                  </tr>
                ))}
              </tbody>
            </Table>
            <div className="cellAction"></div>
          </div>
        </Col>
      </Row>
    </Container>
  );
};

export default OrderScreen;
