import React, { useContext, useEffect, useState } from 'react';
import { Button, Container } from 'react-bootstrap';
import Form from 'react-bootstrap/Form';
import Helmet from 'react-helmet';
import { useNavigate } from 'react-router-dom';
import { Store } from '../../../Store';
import axios from 'axios';

const LoginScreen = () => {
  const [emial, setEmail] = useState(undefined);
  const [password, setPassword] = useState(undefined);
  const credentials = {
    email: emial,
    password: password,
  };
  const { state, dispatch: ctxDispatch } = useContext(Store);
  const navigate = useNavigate();

  const handleClick = async (e) => {
    e.preventDefault();
    ctxDispatch({ type: 'LOGIN_START' });
    try {
      const res = await axios.post(
        'http://localhost:5000/api/login',
        credentials
      );
      console.log(res);
      if (res.data.isAdmin) {
        ctxDispatch({ type: 'LOGIN_SUCCESS', payload: res.data });

        navigate('/admin');
      } else {
        ctxDispatch({
          type: 'LOGIN_FAILURE',
          payload: { message: 'You are not allowed!' },
        });
      }
    } catch (err) {
      ctxDispatch({ type: 'LOGIN_FAILURE', payload: err.response.data });
    }
  };

  return (
    <div className="login">
      <Helmet>
        <meta charSet="utf-8" />
        <title>Login page</title>
        <meta name="description" content="Get all furniture in kerala" />
        <link rel="canonical" href="http://mysite.com/example" />
      </Helmet>
      <h1 className="stroke-head mb-5">Login to Admin panel</h1>
      <Container className="border rounded p-4">
        <Form.Group className="mb-3 " controlId="formBasicEmail">
          <Form.Label>Email id</Form.Label>
          <Form.Control
            onChange={(event) => setEmail(event.target.value)}
            type="email"
            placeholder="Enter email"
          />
        </Form.Group>

        <Form.Group className="mb-3" controlId="formBasicPassword">
          <Form.Label>Password</Form.Label>
          <Form.Control
            onChange={(event) => setPassword(event.target.value)}
            type="password"
            placeholder="Password"
          />
        </Form.Group>
        <div style={{ color: 'darkred' }}>
          {state.error ? state.error.message : state.error}
        </div>
        <Button onClick={handleClick} variant="primary" type="submit">
          Login
        </Button>
      </Container>
    </div>
  );
};

export default LoginScreen;
