import { createContext, useReducer } from 'react';

export const Store = createContext();

const initialState = {
  fav: { favItems: JSON.parse(localStorage.getItem('favItems')) || [] },
  subscribe: JSON.parse(localStorage.getItem('subscribe')) || false,
  user: JSON.parse(localStorage.getItem('user')) || null,
};

function reducer(state, action) {
  switch (action.type) {
    case 'FAV_ADD_ITEM':
      const newItem = action.payload;
      const existItem = state.fav.favItems.find(
        (item) => item.id === newItem.id
      );
      const favItems = existItem
        ? state.fav.favItems.map((item) =>
            item.id === existItem.id ? newItem : item
          )
        : [...state.fav.favItems, newItem];
      localStorage.setItem('favItems', JSON.stringify(favItems));
      return { ...state, cart: { ...state.fav, favItems } };

    case 'FAV_REMOVE_ITEM': {
      const favItems = state.fav.favItems.filter(
        (item) => item.id !== action.payload.id
      );
      localStorage.setItem('favItems', JSON.stringify(favItems));
      return { ...state, fav: { ...state.fav, favItems } };
    }
    case 'SUBSCRIBE': {
      localStorage.setItem('subscribe', JSON.stringify('true'));
      return { subscribe: true };
    }

    case 'LOGIN_START':
      localStorage.setItem(
        'user',
        JSON.stringify({
          user: null,
          loading: true,
          error: null,
        })
      );
      return {
        user: null,
        loading: true,
        error: null,
      };
    case 'LOGIN_SUCCESS':
      localStorage.setItem(
        'user',
        JSON.stringify({
          user: action.payload,
          loading: false,
          error: null,
        })
      );
      return {
        user: action.payload,
        loading: false,
        error: null,
      };
    case 'LOGIN_FAILURE':
      localStorage.setItem(
        'user',
        JSON.stringify({
          user: null,
          loading: false,
          error: action.payload,
        })
      );
      return {
        user: null,
        loading: false,
        error: action.payload,
      };
    case 'LOGOUT':
      localStorage.setItem(
        'user',
        JSON.stringify({
          user: null,
          loading: false,
          error: null,
        })
      );
      return {
        user: null,
        loading: false,
        error: null,
      };

    default:
      return state;
  }
}

export function StoreProvider(props) {
  const [state, dispatch] = useReducer(reducer, initialState);
  const value = { state, dispatch };
  return <Store.Provider value={value}>{props.children}</Store.Provider>;
}
