import React, { useContext, useEffect } from 'react';
import { Button, Card } from 'react-bootstrap';
import Helmet from 'react-helmet';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import FooterMid from '../../components/FooterMid';
import FooterTop from '../../components/FooterTop';
import FormSub from '../../components/FormSub';
import HeaderLow from '../../components/HeaderLow';
import HeaderMid from '../../components/HeaderMid';
import HeaderTop from '../../components/HeaderTop';
import MobileHead from '../../components/MobileHead';
import { Store } from '../../Store';
import '../homescreen/HomeScreen.css';

export default function FavouriteScreen() {
  const notify = () => toast(`Removed from WishList!`);
  const removeFromFavHandler = (e) => {
    notify();
    ctxDispatch({
      type: 'FAV_REMOVE_ITEM',
      payload: {
        id: e.id,
      },
    });
  };
  const { state, dispatch: ctxDispatch } = useContext(Store);
  const { fav } = state;
  useEffect(() => {
    console.log(fav.favItems);
  });

  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Favourite page</title>
        <meta name="description" content="Get all furniture in kerala" />
        <link rel="canonical" href="http://mysite.com/example" />
      </Helmet>
      <header className="App-header">
        {window.innerWidth >= 780 ? (
          <>
            <HeaderTop />
            <HeaderMid />
            <HeaderLow />
          </>
        ) : (
          <MobileHead />
        )}
      </header>
      <main>
        <div className="text-center" style={{ fontFamily: 'pangramregular' }}>
          <h3>
            <span className="stroke-head">Favourite Screen</span>
          </h3>
          <div className="border p-3 m-3">
            <div className="bst-seller ">
              <div className="fav-align">
                {fav.favItems.map((e) => (
                  <Card className="pic-hover mb-3">
                    <i
                      style={{ color: 'red', backgroundColor: 'orange' }}
                      onClick={() => removeFromFavHandler(e)}
                      className="fa fa-heart icon-fav"
                      aria-hidden="true"
                    ></i>{' '}
                    <a href={`/query/${e.id}`} style={{ color: 'black' }}>
                      <Card.Img
                        className="img-fluid img-nl"
                        variant="top"
                        src={e.image}
                      />
                      <Card.Body className="p-0 mt-1">
                        <p>{e.name ? e.name : ''}</p>
                      </Card.Body>
                    </a>
                  </Card>
                ))}
              </div>
            </div>
            <a style={{ color: 'white' }} href="/">
              <Button>Add more favourite items</Button>
            </a>
          </div>
        </div>
      </main>
      <footer>
        <FormSub />
        <FooterTop />
        <FooterMid />
      </footer>
    </>
  );
}
