import React, { useContext, useState } from 'react';
import { Button, Form } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import { Store } from '../Store';
import { toast } from 'react-toastify';
import axios from 'axios';

export default function FormSub() {
  const notify = () => toast(`SUBSCRIBED....!`);

  const [credentials, setCredentials] = useState({
    number: undefined,
  });

  const navigate = useNavigate();
  const { state, dispatch: ctxDispatch } = useContext(Store);
  console.log(state);
  const handleChange = (e) => {
    setCredentials({ number: parseInt(e.target.value) });
  };

  const handleClick = async (e) => {
    e.preventDefault();
    try {
      const res = await axios.post(
        'http://localhost:5000/api/subscribe/',
        credentials
      );
      if (res.data) {
        notify();
        ctxDispatch({ type: 'SUBSCRIBE' });
        navigate('/');
      } else {
        console.log('failed');
      }
    } catch (err) {
      console.log('mot worked');
    }
  };

  return (
    <>
      <div className="text-center m-3">
        <span className="stroke-head">SUBSCRIBE AND REACHOUT SOON</span>

        {state.subscribe === false ? (
          <div className="stroke-body">
            <div className="mb-3 stroke-body1 d-flex">
              <Form.Control
                type="number"
                placeholder="Mobile Number"
                id="number"
                onChange={handleChange}
                className="lInput"
                required
              />
              <Button
                variant="warning"
                onClick={handleClick}
                className="lButton"
              >
                Submit
              </Button>
            </div>
          </div>
        ) : (
          <div className="text-center m-3">
            <Button
              style={{ width: '30vw' }}
              className="stroke-btn"
              variant="success"
              type="submit"
            >
              SUBSCRIBED
            </Button>
          </div>
        )}
      </div>
    </>
  );
}
