import React, { useContext } from 'react';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Store } from '../../../Store';
import { Card, Container } from 'react-bootstrap';
import useFetch from '../../../hooks/useFetch';
import Loader from '../../../components/Loader';

export default function NewLauches() {
  const notify = () => toast(`Added to WishList!`);

  const addToFavHandler = (props) => {
    notify();
    ctxDispatch({
      type: 'FAV_ADD_ITEM',
      payload: {
        id: props._id,
        image: props.image,
        name: props.name,
      },
    });
  };
  const { state, dispatch: ctxDispatch } = useContext(Store);
  const { fav } = state;
  const { data, loading } = useFetch('http://localhost:5000/api/newLaunch/');
  const slideLeft = () => {
    let slider = document.getElementById('slider-new');
    slider.scrollLeft -= 200;
  };

  const slideRight = () => {
    let slider = document.getElementById('slider-new');
    slider.scrollLeft += 200;
  };
  return (
    <Container className="bst-container">
      <hr />
      <h2 className="with-stroke text-center ">
        <span className="stroke-head">New Launches</span>
      </h2>
      <div className="bst-seller ">
        <div className="cont-category">
          <img
            src="https://www.svgrepo.com/show/257665/left-arrow-next.svg"
            className="lefttag border img-fluid opacity-50 cursor-pointer hover:opacity-100"
            onClick={slideLeft}
            alt="left"
          />
          <div
            id="slider-new"
            className="best-align w-full h-full overflow-x-scroll scroll whitespace-nowrap scroll-smooth scrollbar-hide"
          >
            {loading ? (
              <Loader />
            ) : (
              data?.map((e) => (
                <div>
                  <Card key={e._id} className="pic-hover">
                    <i
                      onClick={() => addToFavHandler(e)}
                      className={
                        fav.favItems.find((y) => y.id === e._id)
                          ? 'fa fa-heart icon-fav bg-danger'
                          : 'fa fa-heart icon-fav'
                      }
                      aria-hidden="true"
                    ></i>{' '}
                    <a
                      href={`/query/newLaunch/${e._id}`}
                      style={{ color: 'black' }}
                    >
                      <Card.Img
                        className="img-fluid"
                        variant="top"
                        src={e.image}
                      />
                      <Card.Body className="p-0 mt-1">
                        <p>{e.name}</p>
                      </Card.Body>
                    </a>
                  </Card>
                </div>
              ))
            )}
          </div>
          <img
            src="https://www.svgrepo.com/show/5572/right-arrow.svg"
            className="lefttag border img-fluid opacity-50 cursor-pointer hover:opacity-100"
            onClick={slideRight}
            alt="right"
          />
        </div>
      </div>
      <hr />
    </Container>
  );
}
