import React, { useContext, useState } from 'react';
import './Productscreen.css';
import { useNavigate, useParams } from 'react-router-dom';
import Header from './components/Header';
import { Button, Card } from 'react-bootstrap';
import HeaderTop from '../../components/HeaderTop';
import HeaderMid from '../../components/HeaderMid';
import HeaderLow from '../../components/HeaderLow';
import MobileHead from '../../components/MobileHead';
import FormSub from '../../components/FormSub';
import FooterTop from '../../components/FooterTop';
import FooterMid from '../../components/FooterMid';
import Helmet from 'react-helmet';
import closebtn from './closebtn.png';
import useFetch from '../../hooks/useFetch';
import Loader from '../../components/Loader';
import { Store } from '../../Store';
import { toast } from 'react-toastify';

export default function ProductScreen() {
  const params = useParams();
  const { slug } = params;
  const navigate = useNavigate();

  const [product, setProduct] = useState('hide');
  const [productCont, setProductCont] = useState('');
  const [productImg, setProductImg] = useState('');
  const [productId, setProductId] = useState('');

  const notify = () => toast(`Added to WishList!`);
  const addToFavHandler = (propimg, propid) => {
    notify();
    ctxDispatch({
      type: 'FAV_ADD_ITEM',
      payload: {
        id: propid,
        image: propimg,
        name: '',
      },
    });
  };

  const { state, dispatch: ctxDispatch } = useContext(Store);
  const { fav } = state;
  const [page, setPage] = useState(1);
  const handlepage = (e) => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth',
      /* you can also use 'auto' behaviour
         in place of 'smooth' */
    });
    const name = page + e;
    setPage(name);
  };

  const { data, loading } = useFetch(
    `http://localhost:5000/api/products/category/${slug}?page=${page}&limit=16`
  );
  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Category page</title>
        <meta name="description" content="Get all furniture in kerala" />
        <link rel="canonical" href="http://mysite.com/example" />
      </Helmet>
      <header className="App-header">
        {window.innerWidth >= 780 ? (
          <>
            <HeaderTop />
            <HeaderMid />
            <HeaderLow />
          </>
        ) : (
          <MobileHead />
        )}
      </header>

      <main>
        <div className={productCont}>
          <div>
            <Header slug={slug} />
          </div>
          <div>
            <h5 className="with-stroke text-center">
              <span className="stroke-head">
                <span style={{ fontSize: '30px', color: 'brown' }}>
                  {slug.replaceAll('-', ' ').charAt(0).toUpperCase() +
                    slug.replaceAll('-', ' ').slice(1)}
                </span>{' '}
                are Listed below
              </span>
            </h5>
            <h5 className="with-stroke text-center">
              <span className="stroke-head">Shop Now</span>
            </h5>
          </div>

          <div className="supreme productItem">
            {loading ? (
              <Loader />
            ) : (
              data?.map((e) => (
                <Card>
                  <Card.Img
                    loading="lazy"
                    className="img-fluid lazy"
                    alt="I'm an image!"
                    onClick={() => {
                      setProductCont('product-all');
                      setProduct('product-details');
                      setProductImg(e.image);
                      setProductId(e._id);
                    }}
                    src={e.image}
                  />
                </Card>
              ))
            )}
          </div>
          <div className="text-center">Page {page}</div>
          <div className="text-center ">
            <button
              disabled={page === 1 && true}
              style={{ height: '35px', width: '40vw' }}
              className="border rounded m-2 "
              onClick={() => handlepage(-1)}
            >
              Previous page
            </button>
            <button
              disabled={data.length < 12 && true}
              style={{ height: '35px', width: '40vw' }}
              className="border rounded mt-4 "
              onClick={() => handlepage(1)}
            >
              Next page
            </button>
          </div>
        </div>
        <div className={product}>
          <i
            onClick={() => addToFavHandler(productImg, productId)}
            className={
              fav.favItems.find((y) => y.id === productId)
                ? 'fa fa-heart iconproduct-fav bg-danger'
                : 'fa fa-heart iconproduct-fav'
            }
            ssssssss
            aria-hidden="true"
          ></i>{' '}
          <img
            className="img-fluid rounded popup-img"
            src={productImg}
            alt="logo"
          />
          <img
            onClick={() => {
              setProductCont('');
              setProduct('hide');
              setProductImg('');
              setProductId('');
            }}
            src={closebtn}
            alt="close"
            className="close-btn img-fluid"
          />
          <h1>
            <Button
              style={{ width: '100%', fontWeight: 'bold' }}
              onClick={() => navigate(`/query/${productId}`)}
              variant="warning"
            >
              Order Now
            </Button>
          </h1>
        </div>
      </main>

      <footer>
        <FormSub />
        <FooterTop />
        <FooterMid />
      </footer>
    </>
  );
}
