import './edit.scss';
import Sidebar from '../../../components/sidebar/Sidebar';
import DriveFolderUploadOutlinedIcon from '@mui/icons-material/DriveFolderUploadOutlined';
import { useContext, useEffect, useState } from 'react';
import { Button, Form } from 'react-bootstrap';
import { typeListInputs } from '../../../../formSource';
import { useNavigate, useParams } from 'react-router-dom';
import useFetch from '../../../../hooks/useFetch';
import axios from 'axios';
import { Store } from '../../../../Store';

const Edit = () => {
  const navigate = useNavigate();
  const { type } = useParams();
  const { id } = useParams();
  const { data, loading, error } = useFetch(
    `http://localhost:5000/api/${type}/${id}`
  );

  const { state } = useContext(Store);
  const [file, setFile] = useState('');
  const [text, setText] = useState('');
  const [preview, setPreview] = useState('');

  console.log(file, text);

  const handleClick = async (e) => {
    e.preventDefault();
    await axios.patch(`http://localhost:5000/api/${type}/${id}`, {
      image: data.image,
      name: text,
    });
    navigate(`/admin/${type}`);
  };

  useEffect(() => {
    (state.user === null || state.user.user === null) && navigate('/login');
  }, [state.user]);

  return (
    <div className="new">
      <Sidebar />
      <div className="newContainer">
        <div className="top">
          <h1>Edit {type} Products</h1>
        </div>
        <div className="main-set">
          <div className="text-center">
            <img
              className="img-fluid"
              src={file !== '' ? preview : data.image}
              alt=""
            />
          </div>
          <div className="right">
            <Form onSubmit={() => handleClick(data._id)}>
              <Form.Group className="formInput">
                <Form.Label htmlFor="file">
                  Image upload:{' '}
                  <DriveFolderUploadOutlinedIcon className="icon" />
                </Form.Label>
                <Form.Control
                  type="file"
                  id="file"
                  onChange={(e) => {
                    setFile(e.target.files[0]);
                    setPreview(URL.createObjectURL(e.target.files[0]));
                  }}
                  style={{ display: 'none' }}
                />
              </Form.Group>

              <div className="" key={typeListInputs[0].id}>
                <Form.Group className="mb-3">
                  <Form.Label>{typeListInputs[0].label}</Form.Label>
                  <Form.Control
                    defaultValue={data.name}
                    type="text"
                    placeholder="Enter your product name"
                    onChange={(e) => setText(e.target.value)}
                  />
                </Form.Group>
              </div>
              {text === '' && file === '' ? (
                <Button disabled type="submit">
                  Submit
                </Button>
              ) : (
                <Button type="submit">Submit</Button>
              )}
            </Form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Edit;
